.mission-section {
    position: relative;
    padding: 120px 30px;  /* paddingpadding */
    background-color: #fff;
    text-align: center;
    overflow: hidden;  /*  */
}

/*  */
.mission-content {
    position: relative;
    max-width: 1000px;
    margin: 0 auto;
    padding: 60px 40px;
    z-index: 2;
}

/*  -  */
.mission-section::before {
    content: '';
    position: absolute;
    top: 40px;
    left: 40px;
    width: 100px;
    height: 100px;
    border-left: 3px solid #C5A664;
    border-top: 3px solid #C5A664;
    border-radius: 5px 0 0 0;
    opacity: 0.8;
    z-index: 1;
}

/*  -  */
.mission-section::after {
    content: '';
    position: absolute;
    bottom: 40px;
    right: 40px;
    width: 100px;
    height: 100px;
    border-right: 3px solid #C5A664;
    border-bottom: 3px solid #C5A664;
    border-radius: 0 0 5px 0;
    opacity: 0.8;
    z-index: 1;
}

/*  */
.mission-content::before {
    content: '';
    position: absolute;
    top: 25px;
    left: 50%;
    transform: translateX(-50%);
    width: 80px;
    height: 1px;
    background-color: #C5A664;
    opacity: 0.6;
}

.mission-content::after {
    content: '';
    position: absolute;
    bottom: 25px;
    left: 50%;
    transform: translateX(-50%);
    width: 80px;
    height: 1px;
    background-color: #C5A664;
    opacity: 0.6;
}

.mission-title {
    font-size: 2.5rem;
    color: #C5A664;  /*  */
    margin-bottom: 30px;
    font-weight: 600;
}

.mission-text {
    font-size: 1.2rem;
    line-height: 1.8;
    color: #142613;  /*  */
    max-width: 800px;
    margin: 0 auto;
    font-weight: 500;
}

/*  - L */
@media (max-width: 768px) {
    .mission-section {
        padding: 90px 20px; /*  */
    }
    
    .mission-content {
        padding: 60px 25px; /*  */
    }
    
    .mission-section::before {
        top: 25px;
        left: 25px;
        width: 60px;
        height: 60px;
        border-width: 2px;
    }
    
    .mission-section::after {
        bottom: 25px;
        right: 25px;
        width: 60px;
        height: 60px;
        border-width: 2px;
    }
    
    .mission-content::before,
    .mission-content::after {
        width: 50px;
    }
    
    .mission-title {
        font-size: 2.2rem;
        margin-bottom: 30px; /*  */
        letter-spacing: 0.5px;
    }
    
    .mission-text {
        font-size: 1.1rem;
        line-height: 1.7;
        max-width: 95%;
    }
}

/*  */
@media (max-width: 600px) {
    .mission-section {
        padding: 80px 15px; /*  */
    }
    
    .mission-content {
        padding: 55px 20px; /*  */
    }
    
    .mission-section::before {
        top: 20px;
        left: 20px;
        width: 45px;
        height: 45px;
        border-width: 2px;
    }
    
    .mission-section::after {
        bottom: 20px;
        right: 20px;
        width: 45px;
        height: 45px;
        border-width: 2px;
    }
    
    .mission-content::before,
    .mission-content::after {
        width: 40px;
    }
    
    .mission-title {
        font-size: 2rem;
        margin-bottom: 28px; /*  */
    }
    
    .mission-text {
        font-size: 1.05rem;
        line-height: 1.6;
    }
}

@media (max-width: 480px) {
    .mission-section {
        padding: 70px 15px; /*  */
    }
    
    .mission-content {
        padding: 50px 15px; /*  */
    }
    
    .mission-section::before {
        top: 15px;
        left: 15px;
        width: 35px;
        height: 35px;
        border-width: 2px;
    }
    
    .mission-section::after {
        bottom: 15px;
        right: 15px;
        width: 35px;
        height: 35px;
        border-width: 2px;
    }
    
    .mission-content::before,
    .mission-content::after {
        width: 30px;
    }
    
    .mission-title {
        font-size: 1.6rem;
        margin-bottom: 25px; /*  */
        letter-spacing: 0.5px;
    }
    
    .mission-text {
        font-size: 0.8rem;
        line-height: 1.6;
        font-weight: 800;
    }
    
    /*  */
    .mission-content::before {
        top: 25px; /*  */
    }
    
    .mission-content::after {
        bottom: 25px; /*  */
    }
}

/*  */
@media (max-width: 360px) {
    .mission-section {
        padding: 60px 10px; /*  */
    }
    
    .mission-content {
        padding: 45px 12px; /*  */
    }
    
    .mission-section::before {
        top: 12px;
        left: 12px;
        width: 25px;
        height: 25px;
        border-width: 1.5px;
    }
    
    .mission-section::after {
        bottom: 12px;
        right: 12px;
        width: 25px;
        height: 25px;
        border-width: 1.5px;
    }
    
    .mission-content::before,
    .mission-content::after {
        width: 25px;
    }
    
    .mission-title {
        font-size: 1.4rem;
        margin-bottom: 22px; /*  */
    }
    
    .mission-text {
        font-size: 0.95rem;
        line-height: 1.5;
    }
} 